.App h1 {
  text-align: center;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

.Nav nav {
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(27, 27, 73);
  color: #ccc;
}

.main {
  padding: 10px;
}

.Nav ul {
  display: flex;
}

.Nav li { 
  width: 50%;
  list-style: none;
  margin: 20px;
}

.Nav a {
  color: #ccc;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: rgb(37, 37, 61)
}

a:hover {
  opacity: .8;
}

.Shop {
  text-align: center;
}

.Shop a {
  display: flex;
  align-items: center;
}

.Shop img {
  margin-right: 10px;
}